import React, { Component } from 'react';
import { withRouter, NavLink } from 'react-router-dom';
import { connect } from 'react-redux';
import { Modal } from 'semantic-ui-react';
// import Button from '@material-ui/core/Button';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';


class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isPrivacyModalOpen: false,
    };
  }

  render() {
    return (
      <div>
        <hr className="divider" />
        <div className="footer-container">
          {this.props.onProfilePage ?
            (
              <Modal
                open={this.state.isPrivacyModalOpen}
                onOpen={() => { this.setState({ isPrivacyModalOpen: true }); }}
                onClose={() => { this.setState({ isPrivacyModalOpen: false }); }}
                trigger={(<div className="footer-link" >Privacy Policy</div>)}
              >
                <Modal.Content>
                  <Modal.Header className="modal-header">Crushy Privacy Policy</Modal.Header>
                  <Modal.Content>
                    <hr className="priv-modal-break" />
                    <Modal.Description>
                      <div className="privacypolicy-content-container">
                        <p className="privacypolicy-text"><b>What we collect</b></p>
                        <p className="privacypolicy-text">We collect your Instagram handle as well as the Instagram handles of people you crush on.</p>
                        <br /><br />
                        <p className="privacypolicy-text"><b>How we will use your information</b></p>
                        <p className="privacypolicy-text">We will only use your information to check if you have matched with your crushes. We guarantee nobody will be able to see your crushes except for you and your matches.</p>
                      </div>
                    </Modal.Description>
                  </Modal.Content>
                </Modal.Content>
              </Modal>) :


              <NavLink className="footer-link" exact to="/privacypolicy">
                Privacy Policy
              </NavLink>
        }

          <a href="#" onClick={() => { this.props.history.push('/profile'); }} className="footer-link">Contact</a>
        </div>

      </div>
    );
  }
}

export default withRouter(connect(null, { })(Footer));
